






import { Component, Vue } from "vue-property-decorator";

@Component
export default class ShowWord extends Vue {
  private clicked() {
    this.$root.$emit("word-clicked");
  }
  get word() {
    return this.$store.state.word;
  }


  public bgcolor ="black";
  mounted(){
    if(Math.random()>0.5){
      this.bgcolor = "red";
    } else {
      this.bgcolor ="black";
    }
  }
}
