








import { Component, Vue } from "vue-property-decorator";
import HelloWorld from "@/components/HelloWorld.vue";
import Menu from "@/components/Menu.vue";
import InputScreen from "@/components/InputScreen.vue";
import ShowWord from "@/components/ShowWord.vue";
import LookAround from "@/components/LookAround.vue";

@Component({
  components: { HelloWorld, Menu, InputScreen, ShowWord, LookAround }
})
export default class App extends Vue {
  private view: any = HelloWorld;

  mounted() {
    this.$root.$on("front-page-clicked", () => {
      this.view = Menu;
    });
    this.$root.$on("start-button-clicked", () => {
      this.view = InputScreen;
    });
    this.$root.$on("fertig-clicked", () => {
      this.view = ShowWord;
    });
    this.$root.$on("word-clicked", () => {
      this.view = LookAround;
    });
  }
}
